body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: 'Quicksand', sans-serif;
  font-size: 24px;
  background-color: rgba(247,235,98, .25);
}

  /* Navbar */
.navbar {
  background-color: #0457A0;
  box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
}
.navbar li {
  display: inline-block;
  text-align: center;
}
.nav-item1 {
  width: 20%;
}
.nav-item2 {
  width: 60%;
}
.nav-item3 {
  width: 20%;
}
li {
  color: #fff;
  padding: 15px;
  font-size: 20px;
}

/* Jumbotron */
.jumbotron {
  background-image: url(/clicky-game/static/media/background.8fa0a138.jpeg);
  position: relative;
  color: #0457A0;
  text-align: center;
}


/* Cards */
.card {
  max-width: 250px;
  max-height: 250px;
  margin: 25px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  border: 3px solid #0457A0;
}
img {
  max-width: 243.5px;
  max-height: 244px;
  padding: 0;
}
.flex-fill {
   flex:1 1 auto;
}

/* Footer */
.footer {
  background-color: #0457A0;
  color: #fff;
  text-align: center;
  padding: 12px;
  margin-top: 55px;
  width: 100%;
  font-size: 15px;
  position: relative;
  bottom: 0;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

